const initMap = () => {
    ymaps.ready(['AnimatedLine']).then(init);

    function init(ymaps) {
        var myMap = new ymaps.Map("map", {
            center: [43.060158, 131.876833],
            zoom: 11
        }, {
            searchControlProvider: 'yandex#search'
        });

        //   .
        var firstAnimatedLine = new ymaps.AnimatedLine([
            [43.114899, 131.893716],
            [43.099299, 131.878087],
            [43.079959, 131.867686],
            [43.068456, 131.843626],
            [43.072037, 131.815502],
            [43.051216, 131.779023],
            [43.051216, 131.779023],
            [43.003460, 131.746137],
            [42.983214, 131.744836],
            [42.969267, 131.766172],
            [42.946907, 131.811965],
            [42.930849, 131.812746],
            [42.924157, 131.835382],
            [42.957037, 131.926709],
            [43.020454, 131.947524],
            [43.085902, 131.872589],
            [43.085902, 131.872589]
        ], {}, {
            //  .
            strokeColor: "#ED4543",
            //   .
            strokeWidth: 5,
            //   .
            animationTime: 30000
        });

        myMap.geoObjects
            .add(firstAnimatedLine)
            //   
            .add(new ymaps.Placemark([43.114899, 131.893716], {
                balloonContent: '   ',
                iconCaption: '   '
            }, {
                preset: 'islands#blueAutoCircleIcon',
                iconColor: 'red'
            }));

        //   .
        function playAnimation() {
            firstAnimatedLine.animate()
                .then(function () {
                    playAnimation();
                })
        }
        //   .
        playAnimation();

    }

}

export { initMap }